@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Grandstander:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@700&display=swap);
body {
  background-color: #030f21;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

hr{
  width: 50%;
  border :1px solid #3ed04f;
}

.navigation {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 25px 10px 25px;
  padding: 5px 10px 5px 10px;
  /* background-color: #1a1a2e;
  border-top:5px solid #e94560; */

}

.navbar-item {
  align-items: center;
  padding: 5px 15px 5px 10px;
  border-right: 1px solid;
  color: #3ed04f;
  font-family: 'Grandstander', cursive;
  font-size:20px;
  text-decoration: none;
}

.navbar-item.cart-icon{
  border-right: none;
  display: flex;
}

.navbar-item.cart-icon img{
  width:30px;
  margin-left: 10px;
}

.navbar-item.cart-icon span{
  margin-left: 10px;
}


.about-container {
  background-image: url("/images/about-bg.jpg");
  background-size: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: -1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-info {
  width: 60vw;
  height: 60vh;
  background-color: #6d6d6d85;
  border-radius: 5%;
  padding: 5px 5px 5px 10px;
  color: white;
}

.about-info .title {
  color: rgb(27, 2, 87);
  text-align: center;
  font-size: 34px;
}

.about-info p {
  font-size: 22px;
  font-family: "Roboto Condensed", sans-serif;
}

.banner{
    margin-top : 40px;
    width:100%;
    text-align: center;
    justify-content:center;
}

.title{
    font-family: 'Permanent Marker', cursive;
    font-size: 26px;
    background : -webkit-gradient(linear, left bottom, right top, from(#004d7a), color-stop(#008793), color-stop(#00bf72), to(#a8eb12));
    background : linear-gradient(to right top, #004d7a, #008793, #00bf72, #a8eb12);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.banner img{
    max-width: 90%;
}
.product {
  width: 30%;
  border: 1px solid #000;
  background-color: #1a1a2e;
  margin: 0px 5px 20px 5px;
  position: relative;
  overflow: hidden;
}

.product img {
  width: 100%;
  height: 100%;
  min-height: 400px;
  -webkit-transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
  transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.product .info-holder {
  width: 100%;
  height: 20%;
  position: absolute;
  bottom: 0px;
  background-color: #000000d6;
  display: flex;
  justify-content: space-around;
  align-items: baseline;
  padding-top: 10px;
}

.product:hover img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.product-title {
  font-size: 26px;
  font-family: 'Fredoka One', cursive;
  color: #fc3948;
}

.product-price {
  color: #fff;
  font-family: "Balsamiq Sans", cursive;
  font-size: 20px;
}

.product-by{
  color: #0cc26b;
  font-family: 'Grandstander', cursive;
}

.add-to-cart {
  background-color: #0cc26b;
  font-family: 'Grandstander', cursive;
  font-weight: 700;
  border-radius: 10px;
  border: none;
  font-size: 18px;
  padding: 5px 10px 5px 10px;
  color: white;
  cursor: pointer;
  opacity: 0;
  position: absolute;
  right: 0px;
  left: 0px;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

.product:hover .add-to-cart {
  opacity: 1;
  top: 10px;
  left:10px;
}

.notification-container{
  display: none;
  position: fixed;
  width: 100%;
  left:25%;
  top:20px;
  opacity: 1;
  -webkit-transition: 2s opacity;
  transition: 2s opacity;
}

.fade-out {
  opacity: 0;
}

.notification {
  display: flex;
  border-radius: 15px;
  padding: 15px;
  border: 1px solid black;
  margin: 10px;
  width: 50%;
  align-items: center;
  /* background-color: rgba(255, 255, 255, 0.678); */
  background-color: rgb(3 15 33 / 55%);
  box-shadow: 0px 0px 10px 10px white;
}

.notification-message {
  font-family: "Balsamiq Sans", cursive;
  font-size: 22px;
  color: white;
}

.tick {
  margin-right: 10px;
}

.tick img {
  width: 40px;
}
.close {
  margin-left: auto;
}

.close img {
  width: 25px;
  cursor: pointer;
}


.contact-container {
  background-image: url("/images/contact-bg.jpg");
  background-size: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: -1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-info {
  width: 60vw;
  height: 60vh;
  background-color: #6d6d6d85;
  border-radius: 5%;
  padding: 5px 5px 5px 10px;
  color: white;
}

.contact-info .title {
  color: rgb(27, 2, 87);
  text-align: center;
  font-size: 34px;
}

.contact-info-container {
  margin: 0px 0px 10px 30px;
}

.info-name {
  color: #222831;
  font-size: 22px;
  font-weight: 700;
  color: white;
}

.info-description {
  color: #1a1a2e;
  font-size: 26px;
  font-weight: 700;
}

.cart-container {
  text-align: center;
}

.cart-title {
  font-family: "Permanent Marker", cursive;
  font-size: 40px;
  background: -webkit-gradient(linear, left bottom, right top, from(#004d7a), color-stop(#008793), color-stop(#00bf72), to(#a8eb12));
  background: linear-gradient(to right top, #004d7a, #008793, #00bf72, #a8eb12);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cart-item {
  margin: auto;
  border: 2px solid white;
  color: white;
  padding: 15px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;

}

.cart-item.cart-header {
  margin-bottom: 20px;
  border: 1px solid #fc3948;
  color: #fc3948;
}

.cart-item div {
  /* border-left: 1px solid white; */
  text-align: center;
  font-size: 22px;
  font-weight: 700;
}

.cart-item img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.cart-item .change-qty{
  display: flex;
}

.cart-item .change-qty img{
  width: 35px;
  padding:10px;
}


.cart-header div:nth-child(1),
.cart-item div:nth-child(1) {
  width: 20%;
  border-left: none;
}
.cart-header div:nth-child(2),
.cart-item div:nth-child(2) {
  width: 20%;
}
.cart-header div:nth-child(3),
.cart-item div:nth-child(3) {
  width: 15%;
}
.cart-header div:nth-child(4),
.cart-item div:nth-child(4) {
  width: 15%;
}
.cart-header div:nth-child(5),
.cart-item div:nth-child(5) {
  width: 10%;
}
.cart-header div:nth-child(6),
.cart-item div:nth-child(6) {
  width: 15%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.products-container {
  margin-top: 40px;
}
.products-container .products-title {
  text-align: center;
  font-family: "Fredoka One", cursive;
  font-size: 40px;
  margin: 50px 0px 50px 0px;
}
.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 30px 0px 50px 0px;
}


.highlight{
  width: 300px;
  margin: auto;
  background: url('/images/yellow-brushstroke.png');
  background-repeat: no-repeat;
  background-size: 100% 95%;
  padding: 8px 0;
  color: #fc3948;
}


